  /* textbox */
    *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 'poppins'sans-serif;
  
  }  


  /* parent div*/
  .Request-container{
    display: flex; 
    justify-content: left;
    align-items: left;
    min-height: 100vh;
    flex-direction: column;
    gap:30px;
    background:rgb(255, 255, 255);

  }

 .Request_Box{
    position: relative;
    width: 16rem;

  } 
  
  /* inputbox postion,color,width*/
   .Request_Box input{
     width: 100%; 
     padding: 10px;
     top:130px;
     left: 30px;
     border:2px solid rgb(0, 0, 0) ;
     border-radius: 10px; 
     outline: none;
     color: #080808;
     position: relative;
     font-size: 1em;
     transition: 0.5s; 
     cursor: pointer;

  } 
  
  /* textbox lable position and style (inside the textbox span is placed)*/
  .Request_Box span
  {
    position: relative;
    top: 95px;
    left: 50px;
    padding: 18px;
    pointer-events: none; 
    font-size:1em ;
    color: rgba(255,255,255,0,25);
    text-transform: uppercase;
    transition: 0.5s;

  }
  
  
  /* span style for border left,right,radius,color */
  .Request_Box input:valid~span,
  .Request_Box input:focus~span
  {
    color: #16ec8c;
    top:120px;       
    border-radius: 10px; 
    transform: translateX(10px)translateY(-7px); 
    font-size: 0.65em;
    padding: 0 10px;
    background: #1d2b3a;
    border-left:2px solid #0d00ff; 
    border-right:2px solid  #0d00ff;   
    letter-spacing: 0.2em;

  }  

  /* shadeing the lable to top of textbox */
  .Request_Box:nth-child(2) input:valid~span,
  .Request_Box:nth-child(2) input:focus~span
  {
    background: rgb(251, 249, 248);
    color: rgb(90, 3, 70);
    border-radius: 3px;

  }  
  
  /* if textbox is clicked then the border color change  */
  .Request_Box input:valid,
  .Request_Box input:focus
  {
    border: 3px solid #3716f0;

  }   
  
