/* Styles for horizontal scroll bar */
::-webkit-scrollbar {
  width: 1rem; /* Set the width of the scroll bar */
  height: 1rem; /* Set the height of the scroll bar */
  transition: width 0.3s ease; /* Add transition effect */
  scroll-behavior: smooth; /* Add smooth scrolling behavior */
}

/* Styles for the track of the scroll bar */
::-webkit-scrollbar-track {
  background-color: transparent; /* Set the background color of the track */
}

/* Styles for the thumb of the scroll bar */
::-webkit-scrollbar-thumb {
  background-color: #888; /* Set the color of the thumb */
  border-radius: 1rem; /* Set the border radius of the thumb */
  transition: background-color 0.3s ease; /* Add transition effect */
}

/* Styles when hovering over the thumb */
::-webkit-scrollbar-thumb:hover {
  background-color: #555; /* Set the color of the thumb on hover */
}

/* Hide the vertical scroll bar */
::-webkit-scrollbar-vertical {
  display: none;
}

.prayer-image {
 position: absolute;
 background-image: url('../Img/prayer.jpg');
 top: 5rem;
 left: 50rem;
 width: 48rem;
 height: 44rem;
 background-size: cover;
 z-index: 1;
}

.success-msg,
.error-msg {
 position: absolute;
 top: 40rem;
 left: 50%;
 transform: translateX(-50%);
 font-family: Akshar;
 font-size: 1rem;
 font-weight: bold;
 padding: 0.5rem 1rem;
 border-radius: 0.5rem;
 color: #fff;
}

.success-msg {
 background-color: #4caf50;
}

.error-msg {
 background-color: #f44336;
}