.search-form {
    position: relative;
    display: flex;
    align-items: center;
    margin-top: 10rem;
    margin-left: 5rem;
  }
  
  .searchforfam-input {
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    padding: 8px 16px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-left: 8px;
  }
  